import styled from 'styled-components'
import {Container} from '../../globalstyles';
import {FaMagento} from 'react-icons/fa' ;
import {Link} from "react-router-dom"

export const Nav=styled.nav`
background:#101522;
height:80px;
top:0;
display:flex;
justify-content:center;
align-items:center;
font-size: 1.2rem; 
position: sticky;
z-index:999;
`
export const NavbarContainer = styled(Container)`
display:flex;
justify-content:space-between; 
height:80px;

${Container} 

`
export const NavbarLogo=styled(Link)`
color: #fff;
justify-self:flex-start;
cursor:pointer;
text-decoration:none;
font-size:2rem;
display:flex;
align-items:center; 
 
`

export const NavbarIcon = styled(FaMagento)`
margin-right: 0.5rem;
`

export const MobileIcon = styled.div`
display:none;

@media screen and (max-width:960px){
    display: block;
    position:absolute;
    top:0;
    right:0;
    transform:translate(-100%,60%);
    font-size:1.8rem;
    cursor:pointer;
}
`
export const NavMenu = styled.ul`
display:flex;
align-items:center;
list-style:none;
text-align:center;

@media screen and (max-width:960px){
    display:flex;
    flex-direction:column;
    width:100%;
    height:90vh;
    position:absolute;
    top: 80px;
    left:${({click})=>(click ? 0:' -100%')};
    opacity:1;
    transition: all 0.5s ease;
    background: #101522;
 
}

`

export const NavItem=styled.ul`
  height:80px;
  border-bottom:2px solid transparent;

  &:hover {
    border-bottom:2px solid #4b59f7;
  }

  @media screen and (max-width:960px){
       width:100%;

       &:hover{
           border:none;
       }
  }
`

export const NavLink = styled(Link)`
color:#fff;
align-items:center;
display:flex;
height:100%;
text-decoration:none;
padding: 0.5rem 1rem;

@media screen and (max-width:960px){
    padding:2rem;
    display: table;
    text-align:center;
    width:100%;

&:hover{
   color:#4b59f7;
   transition: all 0.3s ease;
  }
}
`
export const NavItemBtn = styled.li`
@media screen and (max-width:960px){
    display:flex;
    justify-content:center;
    align-items:center; 
    width:100%;
   height:120px;
}
`

export const NavLinkBtn= styled(Link)`

display: flex;
justify-content:center;
align-items:center;

text-decoration:none;
padding: 8px 16px;
width:100%;
height:100%;
border:none;
outline:none;

`